<template>
  <div class="container">
    <LogoTitle :title="''" />
    <BaseInput :formItems="formItems" />
    <div class="button-group">
      <base-button
        :mode="'action'"
        @handleClick="handleClick"
        :isDisabled="isProcessing"
        ></base-button
      >
      <base-button :position="'right'">
        <router-link to="/signin"></router-link>
      </base-button>
    </div>
  </div>
</template>

<script>
import LogoTitle from "../components/UI/LogoTile.vue";
import BaseInput from "../components/UI/BaseInput.vue";
import BaseButton from "../components/UI/BaseButton.vue";
import authorizationAPI from "../apis/authirozation"
import { mapActions } from "vuex";
import { toast } from "../utils/helper";

export default {
  components: {
    LogoTitle,
    BaseInput,
    BaseButton,
  },
  data() {
    return {
      formItems: [
        {
          id: 0,
          name: "",
          type: "text",
          value: "",
        },
        {
          id: 1,
          name: "",
          type: "password",
          value: "",
        },
      ],
      isProcessing: false,
    };
  },
  methods: {
    ...mapActions(["setCurrentUser"]),
    // 
    async handleClick() {
      try {
        this.isProcessing = true;
        const account = this.formItems[0].value;
        const password = this.formItems[1].value;

        // 
        if (account.length < 1 || password.length < 1) {
          toast.fireWarning("");
          return;
        }

        const { data } = await authorizationAPI.signIn({
          account,
          password,
        });

        // 
        if (data.data.user.role !== "admin") {
          toast.fireWarning("");
          this.isProcessing = false;
          return;
        }

        localStorage.setItem("token", data.data.token);

        this.setCurrentUser(data.data.user);

        this.$router.replace("/admin/users");
      } catch (error) {
        console.log("Error", error);
        this.isProcessing = false;
        toast.fireError("");
      }
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/extends.scss";
.container {
  @extend %alphitterContainer;
}
.button-group {
  @extend %button-group;
}
</style>
