import { apiHelper } from "../utils/helper";

//folloer
//following 
export default {
  //id 
  addFollowing({ userId }) {
    return apiHelper.post("/followships", { id: userId });
  },
  //id 
  //following
  deleteFollowing({ followerId, followingId }) {
    return apiHelper.delete(`/followships/${followerId}`, { id: followingId });
  },
};
